import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import PropTypes from "prop-types";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
    FacebookShareButton,
    FacebookIcon,
    TwitterShareButton,
    TwitterIcon,
    LinkedinShareButton,
    LinkedinIcon,
    RedditShareButton,
    RedditIcon
} from "react-share";
import {
    galleryDetailContainer,
    galleryDetailInfoWrapper,
    galleryDetailInfo,
    galleryDetailDesign,
    galleryDetailShare
} from "./styles";
import ImageLoader from "../../ImageLoader";
import { baseURL } from "../../../utils/routes";

class GalleryDetail extends Component {
    static propTypes = {
        designs: PropTypes.shape({
            author: PropTypes.object,
            authorId: PropTypes.number,
            category: PropTypes.object,
            categoryId: PropTypes.number,
            createdAt: PropTypes.string,
            deletedAt: PropTypes.string,
            description: PropTypes.string,
            id: PropTypes.number,
            images: PropTypes.arrayOf(PropTypes.object),
            status: PropTypes.bool,
            title: PropTypes.string,
            updatedAt: PropTypes.string,
            version: PropTypes.number
        }).isRequired
    };

    state = {
        isShareClicked: false
    };

    handleShareClick = () => {
        const { isShareClicked } = this.state;
        this.setState({
            isShareClicked: !isShareClicked
        });
    };

    render() {
        const { designs, match } = this.props;
        const { isShareClicked } = this.state;
        const { title, description, images } = designs;
        const shareURL = baseURL + match.url;

        return (
            <div className={galleryDetailContainer}>
                <div className={galleryDetailInfoWrapper}>
                    <div className={galleryDetailInfo}>
                        <h2>{title}</h2>
                        <p>{description}</p>
                        <div className={galleryDetailShare}>
                            <div
                                className={`${galleryDetailShare}Button`}
                                onClick={this.handleShareClick}
                                role="presentation"
                            >
                                <FontAwesomeIcon icon="share-alt" color="#fff" size="xs" />
                            </div>
                            Share
                            <div
                                className={`${galleryDetailShare}Content`}
                                style={{ maxHeight: !isShareClicked ? "0" : "30rem" }}
                            >
                                <div className={`${galleryDetailShare}Wrapper`}>
                                    <TwitterShareButton url={shareURL}>
                                        <div
                                            className={`${galleryDetailShare}Twitter ${galleryDetailShare}Logo`}
                                        >
                                            <TwitterIcon size={20} />
                                            <div className={`${galleryDetailShare}Text`}>Tweet</div>
                                        </div>
                                    </TwitterShareButton>
                                </div>
                                <div className={`${galleryDetailShare}Wrapper`}>
                                    <FacebookShareButton url={shareURL}>
                                        <div
                                            className={`${galleryDetailShare}Fb ${galleryDetailShare}Logo`}
                                        >
                                            <FacebookIcon size={20} />
                                            <div className={`${galleryDetailShare}Text`}>Share</div>
                                        </div>
                                    </FacebookShareButton>
                                </div>
                                <div className={`${galleryDetailShare}Wrapper`}>
                                    <LinkedinShareButton url={shareURL}>
                                        <div
                                            className={`${galleryDetailShare}Linkedin ${galleryDetailShare}Logo`}
                                        >
                                            <LinkedinIcon size={20} />
                                            <div className={`${galleryDetailShare}Text`}>Share</div>
                                        </div>
                                    </LinkedinShareButton>
                                </div>
                                <div className={`${galleryDetailShare}Wrapper`}>
                                    <RedditShareButton url={shareURL}>
                                        <div
                                            className={`${galleryDetailShare}Reddit ${galleryDetailShare}Logo`}
                                        >
                                            <RedditIcon size={20} />
                                            <div className={`${galleryDetailShare}Text`}>
                                                Submit
                                            </div>
                                        </div>
                                    </RedditShareButton>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={`${galleryDetailDesign}Wrapper`}>
                    {images.length > 0
                        && images.map((image, index) => (
                            <ImageLoader
                                srcLoaded={image.url}
                                className={`${galleryDetailDesign}Image`}
                                key={index}
                            />
                        ))}
                </div>
            </div>
        );
    }
}

export default withRouter(GalleryDetail);
